body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.bg-dark {
  background-color: #2B93D9!important;
}
a {
    color: #FFFFFF;
}
.btn-info {
  color: #fff;
  background-color: #2B93D9;
  border-color: #2B93D9;
}
.lead {
  font-size: 1.25rem;
  font-weight: 500;
  font-style: oblique;
}
img {
  width: 100%;
}

.landing {
  position: relative;
  background: url(/static/media/showcase.774c7f46.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
  margin-top: -24px;
  margin-bottom: -50px;
}

.landing-inner {
  padding-top: 80px;
}

.dark-overlay {
  background-color:rgb(55, 55, 55, 0.4);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.card-form {
  opacity: 0.9;
}

.latest-profiles-img {
  width: 40px;
  height: 40px;
}

.form-control::-webkit-input-placeholder {
  color: #bbb !important;
}

.form-control::-ms-input-placeholder {
  color: #bbb !important;
}

.form-control::placeholder {
  color: #bbb !important;
}
/* Custom  css */
p{
  margin: 0 0 10px;
}
p.copyright {
  text-align: auto;
}
h3 {
  font-size: 25px;
  text-align: center;
}
li {
  margin: 0;
  padding: 0
}
.we {
  text-align: center;
}
.fh5co-footer-link {
  text-align-last: center;
}
.no-bullets {
  list-style-type: none;
}
.help {
  margin-left: 50px;
}
.whowe{
  margin-left: 10px;
}
.service{
  margin-left: 20px
}
.mission{
  margin-left: 50px;
}

/* mobile responsive */
@media screen and (max-width: 320px) {
.h3 {
  font-size: 20px;
  text-align: center;
}
.we {
  font-size: 12px;
  text-align: center;
}
p.copyright {
  font-size: 15px;
}
}
@media screen and (max-width: 768px) {
  .mission {
    margin-left: 8px;
    text-align: center;
}
.we{
  font-size: 13px;
  text-align: center;
}
.whowe {
  margin-left: 0px;
  font-size: 15px;}
  text-align: center;
}
h3 {
  font-size: 15px;
  text-align: center;
}

.no-bullets{
  font-size: 10px;
  text-align: center;
}

@media screen and (max-width: 1024px) {
.whowe {
    margin-left: 0px;
    font-size: 20px;
    text-align: center;
  }
h3 {
  font-size: 20px;
  text-align: center;
}
}

